@import url(https://fonts.googleapis.com/css2?family=Kavoon&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "pokemonHollow";
  src: url(/CatchEmAll/static/media/PokemonHollow.6e5393a6.ttf) format("truetype");
}

@font-face {
  font-family: "pokemonSolid";
  src: url(/CatchEmAll/static/media/PokemonSolid.36d26dbc.ttf) format("truetype");
}

body {
  margin: 0;
  padding: 0;
  max-width: 100vw;
  overflow-x: hidden;
}

.navigation {
  background: #ffcb05;
  border-bottom: 10px solid #e6ac00;
  padding: 0 0;
  width: 100%;
  text-align: center;
  height: 90px;
  line-height: 90px;
  /* z-index: 500; */
  text-align: center;
  /* position: fixed; */
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12),
    0 3px 1px -2px rgba(0, 0, 0, 0.2);
}

.navigation::after {
  content: "";
  display: inline-block;
  width: 350px;
  height: 150px;
  background-image: url(/CatchEmAll/static/media/pokemon-logo.8f80a92d.png);
  background-repeat: no-repeat;
  background-size: 100%;
  position: relative;
  top: 10px;
  left: 0;
  bottom: 0;
}

.cardContainer {
  display: grid;
  padding: 1rem;
  grid-template-columns: repeat(10, 1fr);
  margin-top: 4rem;
  text-align: center;
}

@media (max-width: 650px) {
  .cardContainer {
    grid-template-columns: repeat(3, 1fr);
  }
}

